import React, { useState } from 'react';
import csData from '../../data/cs/contact.json';
import enData from '../../data/en/contact.json';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Tooltip from '@mui/material/Tooltip';
import { useI18n } from '../../context/I18nContext';
import Toast from '../../components/Toast/Toast';
import { ContactFormAlert } from '../../components/ContactFormAlert';
import { EmailCopyButton } from '../../components/EmailCopyButton';

export const ContactPage: React.FC = () => {
  const { locale, t } = useI18n();
  const c = (locale === 'cs' ? csData : enData) as any;
  const [message, setMessage] = useState('');
  const [open, setOpen] = useState(false);
  const [copied, setCopied] = useState<string | null>(null);
  const [severity, setSeverity] = useState<'success' | 'error'>('success');

  const headingId = 'contact-heading';

  const onSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    setOpen(true);
  };

  const handleCopyResult = (text: string, resultSeverity: 'success' | 'error') => {
    setSeverity(resultSeverity);
    setCopied(text);
  };

  return (
    <section className="container" style={{ padding: '24px 0' }} aria-labelledby={headingId}>
      <Typography
        component="h1"
        variant="h3"
        id={headingId}
        sx={{ fontWeight: 800, mb: 2, textAlign: 'center' }}
      >
        {t('contact_heading')}
      </Typography>
      <Paper sx={{ p: { xs: 2, md: 3 } }}>
        <form onSubmit={onSubmit}>
          <Stack spacing={2}>
            <Typography variant="h4" sx={{ m: 0 }}>
              {locale === 'cs' ? 'Napiste mi' : 'Leave me a message'}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {locale === 'cs'
                ? 'Poslete mi prosim zpravu a co nejdrive vam odpovim.'
                : 'Please send me a message and I will get back to you as soon as possible.'}
            </Typography>
            <TextField
              label={locale === 'cs' ? 'Zprava' : 'Message'}
              placeholder={locale === 'cs' ? 'Napiste mi zpravu' : 'Write me a message'}
              multiline
              minRows={4}
              value={message}
              onChange={(event) => setMessage(event.target.value)}
              fullWidth
            />
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={1.5}>
              <Tooltip title={locale === 'cs' ? 'Odeslat dotaz' : 'Send message'}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  aria-label={locale === 'cs' ? 'Odeslat dotaz' : 'Send message'}
                >
                  {locale === 'cs' ? 'Odeslat' : 'Send'}
                </Button>
              </Tooltip>
              {c.email ? (
                <EmailCopyButton
                  email={c.email}
                  locale={locale === 'cs' ? 'cs' : 'en'}
                  onCopyResult={handleCopyResult}
                />
              ) : null}
            </Stack>
          </Stack>
        </form>
      </Paper>
      <ContactFormAlert open={open} onClose={() => setOpen(false)} locale={locale === 'cs' ? 'cs' : 'en'} email={c.email} />
      <Toast open={!!copied} message={copied} onClose={() => setCopied(null)} severity={severity} />
    </section>
  );
};
