import React, { useMemo } from 'react';
import Stack from '@mui/material/Stack';
import csData from '../../data/cs/experience.json';
import enData from '../../data/en/experience.json';
import { useI18n } from '../../context/I18nContext';
import { ExperienceCard, ExperienceEntry } from '../ExperienceCard';

type ExperienceProps = {
  labelledById?: string;
};

const sectionPadding = { padding: '24px 0' };

const sortEntries = (entries: ExperienceEntry[]) => {
  return [...entries].sort((a, b) => {
    const ya = a.end ? parseInt(a.end, 10) : 9999;
    const yb = b.end ? parseInt(b.end, 10) : 9999;
    if (yb !== ya) return yb - ya;
    const sa = parseInt(a.start, 10);
    const sb = parseInt(b.start, 10);
    return sb - sa;
  });
};

export const Experience: React.FC<ExperienceProps> = ({ labelledById }) => {
  const { locale } = useI18n();
  const entries = (locale === 'cs' ? (csData as ExperienceEntry[]) : (enData as ExperienceEntry[]));

  const sortedEntries = useMemo(() => sortEntries(entries), [entries]);

  return (
    <section className="container" style={sectionPadding} aria-labelledby={labelledById}>
      <Stack spacing={2}>
        {sortedEntries.map((entry, index) => (
          <ExperienceCard
            key={`${entry.company}-${entry.role}-${index}`}
            entry={entry}
            isCs={locale === 'cs'}
            animationOrder={index}
          />
        ))}
      </Stack>
    </section>
  );
};
