import React from 'react';
import Stack from '@mui/material/Stack';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import csData from '../../data/cs/projects.json';
import enData from '../../data/en/projects.json';
import { useI18n } from '../../context/I18nContext';
import { ProjectCard, ProjectEntry } from '../ProjectCard';

type ProjectsProps = {
  labelledById?: string;
};

const sectionPadding = { padding: '24px 0' };

export const Projects: React.FC<ProjectsProps> = ({ labelledById }) => {
  const { locale } = useI18n();
  const items = (locale === 'cs' ? csData : enData) as ProjectEntry[];

  return (
    <section className="container" style={sectionPadding} aria-labelledby={labelledById}>
      <Stack spacing={2}>
        {items.length === 0 ? (
          <Paper sx={{ p: 2.5 }}>
            <Typography>Brzy doplnim projekty.</Typography>
          </Paper>
        ) : (
          items.map((project, index) => (
            <ProjectCard
              key={`${project.name}-${index}`}
              project={project}
              animationOrder={index}
            />
          ))
        )}
      </Stack>
    </section>
  );
};
