import React, { useState } from 'react';
import { useI18n } from '../../context/I18nContext';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import Toast from '../Toast/Toast';

export const Footer: React.FC = () => {
  const { t, locale } = useI18n();
  const year = new Date().getFullYear();
  const c: any = locale === 'cs'
    ? require('../../data/cs/contact.json')
    : require('../../data/en/contact.json');

  const [copied, setCopied] = useState<string | null>(null);
  const [severity, setSeverity] = useState<'success' | 'error'>('success');

  const copyEmail = async (e: React.MouseEvent) => {
    e.preventDefault();
    const email: string = c?.email || '';
    try {
      if (navigator && 'clipboard' in navigator) {
        await (navigator as any).clipboard.writeText(email);
      } else {
        const ta = document.createElement('textarea');
        ta.value = email;
        ta.style.position = 'fixed';
        ta.style.opacity = '0';
        document.body.appendChild(ta);
        ta.focus();
        ta.select();
        document.execCommand('copy');
        document.body.removeChild(ta);
      }
      setSeverity('success');
      setCopied(locale === 'cs' ? 'Email byl zkoprovn do schrnky.' : 'Email copied to clipboard.');
    } catch {
      setSeverity('error');
      setCopied(locale === 'cs' ? 'Nepodailo se zkoprovat email.' : 'Failed to copy email.');
    }
  };

  return (
    <Box component="footer" sx={{ borderTop: 1, borderColor: 'divider', mt: 3 }}>
      <Stack className="container" spacing={1.5} sx={{ py: 2 }}>
        {/* Contact row separated from nav and copyright */}


        {/* Nav links */}
        <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ justifyContent: 'center' }}>
          {[
            { href: '#/', label: t('home') },
            { href: '#/education', label: t('education') },
            { href: '#/experience', label: t('experience') },
            { href: '#/projects', label: t('projects') },
            { href: '#/certifications', label: t('certifications') },
            { href: '#/contact', label: t('contact') },
          ].map((l) => (
            <Typography
              key={l.href}
              component="a"
              href={l.href}
              sx={{
                px: 1,
                color: (theme) => theme.palette.mode === 'dark' ? theme.palette.grey[300] : theme.palette.text.secondary,
                '&:hover': {
                  color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.text.primary,
                },
              }}
            >
              {l.label}
            </Typography>
          ))}
        </Stack>

        <Stack direction="row" spacing={1.5} sx={{ justifyContent: 'center', alignItems: 'center' }}>
          {c?.linkedin && (
              <IconButton
                  href={c.linkedin}
                  target="_blank"
                  rel="noreferrer"
                  aria-label="LinkedIn"
                  color="inherit"
                  sx={{ color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.text.secondary }}
              >
                <LinkedInIcon />
              </IconButton>
          )}
          {c?.email && (
            <Typography
              component="a"
              href={`mailto:${c.email}`}
              onClick={copyEmail}
              sx={{
                display: 'inline-flex',
                alignItems: 'center',
                gap: 0.5,
                px: 1,
                cursor: 'pointer',
                userSelect: 'none',
                color: (theme) => theme.palette.mode === 'dark' ? theme.palette.grey[300] : theme.palette.text.secondary,
                '&:hover': {
                  color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.text.primary,
                },
              }}
              title={locale === 'cs' ? 'Kliknutm zkoprujete email' : 'Click to copy email'}
            >
              <MailOutlineIcon fontSize="small" />
              {c.email}
            </Typography>
          )}
        </Stack>

        {/* Copyright */}
        <Typography
          variant="body2"
          textAlign="center"
          sx={{ color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.text.primary }}
        >
           {year} Ji Orsg
        </Typography>
      </Stack>
      <Toast open={!!copied} message={copied} onClose={() => setCopied(null)} severity={severity} />
    </Box>
  );
};
