import React from 'react';
import Button, { ButtonProps } from '@mui/material/Button';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import Tooltip from '@mui/material/Tooltip';
import type { SxProps, Theme } from '@mui/material/styles';

type CopySeverity = 'success' | 'error';

type EmailCopyButtonProps = {
  email: string;
  locale: 'cs' | 'en';
  onCopyResult: (message: string, severity: CopySeverity) => void;
  label?: string;
  tooltip?: string;
  buttonProps?: ButtonProps;
};

const getCopyMessage = (locale: 'cs' | 'en', success: boolean) => {
  if (locale === 'cs') {
    return success ? 'E-mail byl zkopirovan do schranky.' : 'Nepodarilo se zkopirovat e-mail.';
  }
  return success ? 'E-mail copied to clipboard.' : 'Failed to copy e-mail.';
};

const copyToClipboard = async (value: string) => {
  if (navigator && 'clipboard' in navigator) {
    await navigator.clipboard.writeText(value);
    return;
  }
  const textarea = document.createElement('textarea');
  textarea.value = value;
  textarea.style.position = 'fixed';
  textarea.style.opacity = '0';
  document.body.appendChild(textarea);
  textarea.focus();
  textarea.select();
  document.execCommand('copy');
  document.body.removeChild(textarea);
};

export const EmailCopyButton: React.FC<EmailCopyButtonProps> = ({
  email,
  locale,
  onCopyResult,
  label,
  tooltip,
  buttonProps,
}) => {
  const handleClick = async (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
    try {
      await copyToClipboard(email);
      onCopyResult(getCopyMessage(locale, true), 'success');
    } catch {
      onCopyResult(getCopyMessage(locale, false), 'error');
    }
  };

  const tooltipText = tooltip ?? (locale === 'cs' ? 'Zkopirovat e-mail do schranky' : 'Copy e-mail to clipboard');
  const { sx, startIcon, variant, ...buttonRest } = buttonProps ?? {};
  if (buttonRest && Object.prototype.hasOwnProperty.call(buttonRest, 'aria-label')) {
    delete (buttonRest as any)['aria-label'];
  }
  const resolvedVariant = variant ?? 'outlined';
  const defaultSx: SxProps<Theme> | undefined =
    resolvedVariant === 'outlined'
      ? (theme) => ({
          color: theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.primary.main,
          borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : theme.palette.primary.main,
          '&:hover': {
            borderColor: theme.palette.mode === 'dark' ? '#ffffff' : theme.palette.primary.dark,
            backgroundColor:
              theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.08)' : 'rgba(66,81,84,0.06)',
          },
        })
      : undefined;
  const combinedSx: SxProps<Theme> | undefined =
    defaultSx && sx
      ? Array.isArray(sx)
        ? [defaultSx, ...sx]
        : [defaultSx, sx]
      : defaultSx ?? sx;
  const resolvedAriaLabel =
    (buttonProps && typeof (buttonProps as any)['aria-label'] === 'string'
      ? (buttonProps as any)['aria-label']
      : undefined) ?? tooltipText;
  const resolvedLabel = label ?? (locale === 'cs' ? 'E-mail' : 'E-mail');

  return (
    <Tooltip title={tooltipText}>
      <Button
        type="button"
        onClick={handleClick}
        variant={resolvedVariant}
        startIcon={startIcon ?? <MailOutlineIcon />}
        sx={combinedSx}
        aria-label={resolvedAriaLabel}
        {...buttonRest}
      >
        {resolvedLabel}
      </Button>
    </Tooltip>
  );
};
