import React from 'react';
import { useI18n } from '../../context/I18nContext';
import { useConsent } from '../../context/ConsentContext';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

export const CookieConsent: React.FC = () => {
  const { t } = useI18n();
  const { consent, setConsent } = useConsent();

  if (consent) return null;

  return (
    <div role="dialog" aria-live="polite" aria-label={t('cookie_title')} style={{ position: 'fixed', insetInline: 16, bottom: 16, zIndex: 1000 }}>
      <Paper elevation={8} sx={{ p: 2, borderRadius: 3 }}>
        <Stack spacing={1.5}>
          <Typography variant="subtitle1" fontWeight={700}>{t('cookie_title')}</Typography>
          <Typography variant="body2" color="text.secondary">{t('cookie_text')}</Typography>
          <Stack direction="row" spacing={1} flexWrap="wrap">
            <Button onClick={() => setConsent({ necessary: true, preferences: true, analytics: false, marketing: false })} variant="contained" color="primary">
              {t('accept_all')}
            </Button>
            <Button onClick={() => setConsent({ necessary: true, preferences: false, analytics: false, marketing: false })} variant="outlined" color="secondary">
              {t('reject_nonessential')}
            </Button>
          </Stack>
        </Stack>
      </Paper>
    </div>
  );
};
