import React, { useEffect, useMemo, useRef, useState } from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Button from '@mui/material/Button';
import Drawer from '@mui/material/Drawer';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import ButtonGroup from '@mui/material/ButtonGroup';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import LightModeIcon from '@mui/icons-material/LightMode';
import Tooltip from '@mui/material/Tooltip';
import { useI18n } from '../../context/I18nContext';
import { useTheme } from '../../context/ThemeContext';
import Avatar from '@mui/material/Avatar';
import ButtonBase from '@mui/material/ButtonBase';

type NavProps = {
  onNavigate?: () => void;
  active?: 'home' | 'education' | 'experience' | 'projects' | 'certifications' | 'contact';
};

export const Navbar: React.FC<NavProps> = ({ onNavigate, active }) => {
  const { t, locale, setLocale } = useI18n();
  const { theme, toggleTheme } = useTheme();
  const [open, setOpen] = useState(false);
  const [hidden, setHidden] = useState(false);
  const lastY = useRef(0);

  useEffect(() => {
    const onScroll = () => {
      const y = window.scrollY || 0;
      const delta = y - lastY.current;
      const isDown = delta > 0 && y > 64;
      setHidden(isDown);
      lastY.current = y;
    };
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  const close = () => { setOpen(false); onNavigate?.(); };

  const items = useMemo(() => ([
    { key: 'home' as const, label: t('home'), href: '#/' },
    { key: 'education' as const, label: t('education'), href: '#/education' },
    { key: 'experience' as const, label: t('experience'), href: '#/experience' },
    { key: 'projects' as const, label: t('projects'), href: '#/projects' },
    { key: 'certifications' as const, label: t('certifications'), href: '#/certifications' },
    { key: 'contact' as const, label: t('contact'), href: '#/contact' },
  ]), [t]);

  const activeGradient = (mode: 'light' | 'dark') =>
    mode === 'dark'
      ? 'linear-gradient(180deg, #444444 0%, #333333 100%)'
      : 'linear-gradient(180deg, #f7f7f7 0%, #e6e6e6 100%)';

  const LanguageSwitcher: React.FC = () => {
    const renderButton = (code: 'cs' | 'en', index: number) => {
      const isActive = locale === code;
      const title = code === 'cs' ? t('language_name_cs') : t('language_name_en');
      const switchTitle = code === 'cs' ? t('language_toggle_to_cs') : t('language_toggle_to_en');
      const isFirst = index === 0;
      const isLast = index === 1;
      return (
        <Tooltip title={isActive ? title : switchTitle} key={code}>
          <Button
            onClick={() => setLocale(code)}
            variant="outlined"
            size="small"
            aria-label={isActive ? `${title} (${t('language')})` : switchTitle}
            aria-pressed={isActive}
            sx={(theme) => ({
              borderRadius: isFirst
                ? '9999px 0 0 9999px'
                : isLast
                  ? '0 9999px 9999px 0'
                  : 0,
              borderColor:
                theme.palette.mode === 'dark'
                  ? 'rgba(255,255,255,0.5)'
                  : 'rgba(0,0,0,0.2)',
              fontWeight: isActive ? 700 : 500,
              color: isActive
                ? theme.palette.mode === 'dark'
                  ? theme.palette.common.white
                  : theme.palette.primary.main
                : theme.palette.mode === 'dark'
                  ? 'rgba(255,255,255,0.75)'
                  : undefined,
              backgroundColor: isActive
                ? theme.palette.mode === 'dark'
                  ? 'rgba(255,255,255,0.16)'
                  : 'rgba(66,81,84,0.12)'
                : 'transparent',
              transition: 'background-color 120ms ease, color 120ms ease',
              '&:hover': {
                backgroundColor: isActive
                  ? theme.palette.mode === 'dark'
                    ? 'rgba(255,255,255,0.22)'
                    : 'rgba(66,81,84,0.18)'
                  : theme.palette.mode === 'dark'
                    ? 'rgba(255,255,255,0.12)'
                    : 'rgba(66,81,84,0.08)',
              },
            })}
          >
            {code === 'cs' ? 'CZ' : 'EN'}
          </Button>
        </Tooltip>
      );
    };

    return (
      <ButtonGroup
        variant="outlined"
        size="small"
        sx={{ borderRadius: 9999, overflow: 'hidden' }}
        role="group"
        aria-label={t('language')}
      >
        {(['cs', 'en'] as const).map((code, index) => renderButton(code, index))}
      </ButtonGroup>
    );
  };

  const themeTooltip = t('theme_toggle_tooltip');

  const ThemeToggle: React.FC = () => (
    <Tooltip title={themeTooltip}>
      <span style={{ display: 'inline-flex' }}>
        <Button
          onClick={toggleTheme}
          variant="outlined"
          size="small"
          color="inherit"
          aria-label={themeTooltip}
          sx={{
            borderRadius: 9999,
            minWidth: 0,
            px: 1.5,
            borderColor: (theme) => (theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.5)' : 'inherit'),
            color: 'inherit',
          }}
        >
          {theme === 'dark' ? <DarkModeIcon fontSize="small" color="inherit" /> : <LightModeIcon fontSize="small" color="inherit" />}
        </Button>
      </span>
    </Tooltip>
  );

  const NavButtons = (
    <Stack direction="row" spacing={1} sx={{ alignItems: 'center', flexWrap: 'wrap' }}>
      {items.map((it) => (
        <Button key={it.key} href={it.href}
          onClick={close}
          variant={'text'}
          color="inherit"
          sx={{
            borderRadius: 9999,
            background: active === it.key ? ((theme) => activeGradient(theme.palette.mode)) : undefined,
            color: active === it.key ? ((theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : '#1b1b1b') : undefined,
            border: active === it.key ? '1px solid' : undefined,
            borderColor: active === it.key ? 'divider' : undefined,
            boxShadow: active === it.key ? 1 : undefined,
            '&:hover': {
              filter: active === it.key ? 'brightness(1.02)' : undefined,
            }
          }}>
          {it.label}
        </Button>
      ))}
    </Stack>
  );

  return (
    <AppBar position="sticky" color="default" enableColorOnDark elevation={0}
      sx={{
        top: 0,
        transform: hidden ? 'translateY(-110%)' : 'translateY(0)',
        transition: 'transform 220ms ease',
        borderBottom: 1,
        borderColor: 'divider',
        backdropFilter: 'blur(8px)',
        color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.text.primary,
      }}>
      <Toolbar className="container" disableGutters sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
        <ButtonBase href="#/" onClick={close} focusRipple
          sx={{ borderRadius: 2, px: 1, py: 0.5, display: 'inline-flex', alignItems: 'center', gap: 1,
            '&:hover': { opacity: 0.85 }, cursor: 'pointer', color: 'inherit' }}>
          <Avatar alt="Ji Orsg" src="https://qwerjorsag.github.io/MyCV/assets/profile.jpg" sx={{ width: 28, height: 28, borderRadius: 2 }}>J</Avatar>
            <Typography variant="h6" sx={{ fontWeight: 700 }}>
                <Box component="span" sx={{ display: { xs: 'none', md: 'inline' } }}>
                    Ji
                </Box>
                {" "}
                Orsg
            </Typography>
        </ButtonBase>
        <Box sx={{ display: { xs: 'none', md: 'flex' }, mx: 'auto' }}>{NavButtons}</Box>
        <Stack direction="row" spacing={1} sx={{ alignItems: 'center', ml: { xs: 'auto', md: 0 } }}>
          <LanguageSwitcher />
          <ThemeToggle />
          <IconButton sx={{ display: { xs: 'inline-flex', md: 'none' } }} color="inherit" onClick={() => setOpen(true)} aria-label="Open menu">
            <MenuIcon />
          </IconButton>
        </Stack>
      </Toolbar>
      <Drawer anchor="top" open={open} onClose={() => setOpen(false)}>
        <Box sx={{ p: 2, color: (theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : undefined }}>
          <Stack spacing={1}>
            {items.map(it => (
              <Button key={it.key} href={it.href} onClick={close}
                variant={active === it.key ? 'text' : 'outlined'}
                color="inherit"
                sx={{
                  borderRadius: 9999,
                  background: active === it.key ? ((theme) => activeGradient(theme.palette.mode)) : undefined,
                  color: active === it.key ? ((theme) => theme.palette.mode === 'dark' ? theme.palette.common.white : '#1b1b1b') : undefined,
                  border: active === it.key ? '1px solid' : undefined,
                  borderColor: active === it.key ? 'divider' : undefined,
                  boxShadow: active === it.key ? 1 : undefined,
                  '&:hover': {
                    filter: active === it.key ? 'brightness(1.02)' : undefined,
                  }
                }}>
                {it.label}
              </Button>
            ))}
            <Stack direction="row" spacing={1} sx={{ flexWrap: 'wrap' }}>
              <LanguageSwitcher />
              <ThemeToggle />
            </Stack>
          </Stack>
        </Box>
      </Drawer>
    </AppBar>
  );
};
