import React from 'react';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import csData from '../../data/cs/education.json';
import enData from '../../data/en/education.json';
import { useI18n } from '../../context/I18nContext';
import { EducationEntry, EducationCard } from '../EducationCard/EducationCard';

type EducationProps = {
  labelledById?: string;
};

const sectionPadding = { padding: '24px 0' };

const sortEntries = (entries: EducationEntry[]) => {
  return [...entries].sort((a, b) => {
    const ya = a.end ? parseInt(a.end, 10) : 9999;
    const yb = b.end ? parseInt(b.end, 10) : 9999;
    if (yb !== ya) return yb - ya;
    const sa = parseInt(a.start, 10);
    const sb = parseInt(b.start, 10);
    return sb - sa;
  });
};

export const Education: React.FC<EducationProps> = ({ labelledById }) => {
  const { locale } = useI18n();
  const entries = (locale === 'cs' ? csData : enData) as EducationEntry[];
  const sortedEntries = sortEntries(entries);

  return (
    <section className="container" style={sectionPadding} aria-labelledby={labelledById}>
      <Stack spacing={2}>
        {sortedEntries.length === 0 ? (
          <Paper sx={{ p: 2.5 }}>
            <Typography>Brzy doplnm vzdlan.</Typography>
          </Paper>
        ) : (
          sortedEntries.map((entry, idx) => (
            <EducationCard
              key={`${entry.institution}-${idx}`}
              entry={entry}
              isCs={locale === 'cs'}
              animationOrder={idx}
            />
          ))
        )}
      </Stack>
    </section>
  );
};
