import React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { useScrollReveal } from '../../hooks/useScrollReveal';

export type Certification = {
  name: string;
  issuer: string;
  date: string;
  url?: string;
  description?: string;
  logo?: string;
  logoPath?: string;
};

type CertificationCardProps = {
  cert: Certification;
  animationOrder?: number;
};

export const CertificationCard: React.FC<CertificationCardProps> = ({ cert, animationOrder = 0 }) => {
  const direction = animationOrder % 2 === 0 ? 'left' : 'right';
  const { ref, style } = useScrollReveal({
    direction,
    staggerIndex: animationOrder,
  });

  const logoSource = cert.logoPath || cert.logo;
  return (
    <Box sx={{ width: '100%' }}>
      <Paper ref={ref} sx={{ p: 2.5 }} style={style}>
        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} alignItems={{ xs: 'flex-start', sm: 'center' }}>
          {logoSource ? (
            <Avatar
              src={process.env.PUBLIC_URL + logoSource}
              alt={cert.name}
              sx={{ width: 48, height: 48, borderRadius: 2 }}
            />
          ) : null}
          <Stack spacing={0.5} sx={{ flex: 1 }}>
            <Typography variant="h6" sx={{ m: 0 }}>
              {cert.name}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {cert.issuer}
            </Typography>
            {cert.date ? <Typography variant="body2">{cert.date}</Typography> : null}
            {cert.description ? <Typography variant="body2">{cert.description}</Typography> : null}
            {cert.url ? (
              <Button href={cert.url} target="_blank" rel="noreferrer" variant="outlined" sx={{ mt: 1, alignSelf: 'flex-start' }}>
                Detail
              </Button>
            ) : null}
          </Stack>
        </Stack>
      </Paper>
    </Box>
  );
};
