import { useEffect, useMemo, useRef, useState } from 'react';

type Direction = 'left' | 'right' | 'up' | 'down';

export type ScrollRevealOptions = {
  /**
   * Direction from which the element should slide in.
   * Defaults to `left`.
   */
  direction?: Direction;
  /**
   * Distance (in pixels) to travel before the element reaches its resting position.
   * Defaults to 48 on desktop and 20 on small screens.
   */
  distance?: number;
  /**
   * Animation duration in milliseconds.
   * Default: 500 ms.
   */
  durationMs?: number;
  /**
   * Optional base delay in milliseconds applied once the observer triggers.
   */
  delayMs?: number;
  /**
   * Optional multiplier index for creating staggered animations.
   * When provided, stagger delays are calculated as `Math.min(staggerIndex * staggerStepMs, staggerMaxMs)`.
   */
  staggerIndex?: number;
  /**
   * Step (in ms) used for stagger calculation. Default: 40 ms.
   */
  staggerStepMs?: number;
  /**
   * Maximum stagger delay (ms). Default: 200 ms.
   */
  staggerMaxMs?: number;
  /**
   * IntersectionObserver options.
   */
  threshold?: number | number[];
  rootMargin?: string;
  /**
   * Whether the animation should trigger only once.
   * Default: true.
   */
  once?: boolean;
};

export type ScrollRevealResult = {
  ref: (node: HTMLElement | null) => void;
  style: React.CSSProperties;
  isVisible: boolean;
};

const DEFAULT_DISTANCE_DESKTOP = 96;
const DEFAULT_DISTANCE_MOBILE = 20;

/**
 * Reusable hook that reveals an element with a slide-and-fade animation
 * once it enters the viewport.
 */
export const useScrollReveal = (options: ScrollRevealOptions = {}): ScrollRevealResult => {
  const {
    direction = 'left',
    distance,
    durationMs = 500,
    delayMs = 0,
    staggerIndex,
    staggerStepMs = 40,
    staggerMaxMs = 50,
    threshold = 0,
    rootMargin = '0px 0px -10% 0px',
    once = false,
  } = options;

  const nodeRef = useRef<HTMLElement | null>(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const node = nodeRef.current;
    if (!node) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsVisible(true);
            if (once) {
              observer.unobserve(entry.target);
            }
          } else if (!once) {
            setIsVisible(false);
          }
        });
      },
      { threshold, rootMargin },
    );

    observer.observe(node);
    return () => observer.disconnect();
  }, [once, rootMargin, threshold]);

  const computedDistance = useMemo(() => {
    if (typeof distance === 'number') return distance;
    if (typeof window !== 'undefined') {
      const isMobile = window.matchMedia('(max-width: 600px)').matches;
      return isMobile ? DEFAULT_DISTANCE_MOBILE : DEFAULT_DISTANCE_DESKTOP;
    }
    return DEFAULT_DISTANCE_DESKTOP;
  }, [distance]);

  const translateValue = useMemo(() => {
    if (direction === 'up') return `translateY(${isVisible ? 0 : computedDistance}px)`;
    if (direction === 'down') return `translateY(${isVisible ? 0 : -computedDistance}px)`;
    if (direction === 'right') return `translateX(${isVisible ? 0 : -computedDistance}px)`;
    // default left
    return `translateX(${isVisible ? 0 : computedDistance}px)`;
  }, [computedDistance, direction, isVisible]);

  const staggerDelay =
    staggerIndex != null
      ? Math.min(staggerIndex * staggerStepMs, staggerMaxMs)
      : 0;

  const style = useMemo<React.CSSProperties>(() => {
    return {
      opacity: isVisible ? 1 : 0,
      transform: translateValue,
      transitionProperty: 'opacity, transform',
      transitionTimingFunction: 'ease-out',
      transitionDuration: `${durationMs}ms`,
      transitionDelay: `${(isVisible ? delayMs : 0) + staggerDelay}ms`,
      willChange: isVisible ? undefined : 'opacity, transform',
    };
  }, [delayMs, durationMs, isVisible, staggerDelay, translateValue]);

  const ref = (node: HTMLElement | null) => {
    nodeRef.current = node;
  };

  return { ref, style, isVisible };
};
