import React, { createContext, useContext, useEffect, useMemo, useState } from 'react';
import { useConsent } from './ConsentContext';

type Theme = 'light' | 'dark';

type ThemeContextType = {
  theme: Theme;
  setTheme: (t: Theme) => void;
  toggleTheme: () => void;
};

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);
const THEME_KEY = 'pref-theme';

function getSystemTheme(): Theme {
  if (typeof window !== 'undefined' && window.matchMedia) {
    return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
  }
  return 'light';
}

export const ThemeProvider: React.FC<{ children: React.ReactNode }>
  = ({ children }) => {
  const { consent } = useConsent();
  const [theme, setThemeState] = useState<Theme>(getSystemTheme());

  // load persisted only if preferences are allowed
  useEffect(() => {
    if (consent?.preferences) {
      const persisted = localStorage.getItem(THEME_KEY) as Theme | null;
      if (persisted === 'light' || persisted === 'dark') {
        setThemeState(persisted);
      }
    }
  }, [consent?.preferences]);

  useEffect(() => {
    document.documentElement.setAttribute('data-theme', theme);
    if (consent?.preferences) {
      try { localStorage.setItem(THEME_KEY, theme); } catch { /* ignore */ }
    }
  }, [theme, consent?.preferences]);

  const setTheme = (t: Theme) => setThemeState(t);
  const toggleTheme = () => setThemeState(prev => prev === 'light' ? 'dark' : 'light');

  const value = useMemo(() => ({ theme, setTheme, toggleTheme }), [theme]);
  return <ThemeContext.Provider value={value}>{children}</ThemeContext.Provider>;
};

export const useTheme = () => {
  const ctx = useContext(ThemeContext);
  if (!ctx) throw new Error('useTheme must be used within ThemeProvider');
  return ctx;
};

