import { useEffect } from "react";
import { useI18n } from "../../context/I18nContext";

type Props = {
    current: string;
    baseTitle?: string;
};

export const TitleManager: React.FC<Props> = ({ current, baseTitle = "Ji Orsg" }) => {
    const { t } = useI18n();

    useEffect(() => {
        const map: Record<string, string> = {
            home: baseTitle,
            education: `${baseTitle} - ${t('education')}`,
            experience: `${baseTitle} - ${t('experience')}`,
            projects: `${baseTitle} - ${t('projects')}`,
            certifications: `${baseTitle} - ${t('certifications')}`,
            contact: `${baseTitle} - ${t('contact')}`,
        };
        document.title = map[current] ?? baseTitle;
    }, [current, t, baseTitle]);

    return null;
};
