import React from 'react';
import Button from '@mui/material/Button';
import DownloadIcon from '@mui/icons-material/Download';

type Props = {
  href?: string;
  fileName?: string;
  label?: string;
};

export const DownloadCVButton: React.FC<Props> = ({
                                                    href = process.env.PUBLIC_URL + '/assets/CV/cv.pdf',
                                                    fileName = 'cv.pdf',
                                                    label = 'Sthnout CV',
                                                  }) => {
  return (
      <Button
          component="a"
          href={href}
          download={fileName}
          variant="contained"
          color="primary"
          startIcon={<DownloadIcon />}
          aria-label={label}
      >
        {label}
      </Button>
  );
};

export default DownloadCVButton;
