import React, { useMemo } from 'react';
import Avatar from '@mui/material/Avatar';
import Box from '@mui/material/Box';
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import csContact from '../../data/cs/contact.json';
import enContact from '../../data/en/contact.json';
import csSkills from '../../data/cs/skills.json';
import enSkills from '../../data/en/skills.json';
import { useI18n } from '../../context/I18nContext';
import { EmailCopyButton } from '../EmailCopyButton';
import { LinkedInButton } from '../LinkedInButton';

type ContactData = {
  fullName: string;
  location: string;
  email?: string;
  linkedin?: string;
};

type SkillsData = {
  tech?: string[];
  tools?: string[];
};

type HomeHeroProps = {
  onCopyResult: (message: string, severity: 'success' | 'error') => void;
  labelledById?: string;
};

const profileSrc = 'https://qwerjorsag.github.io/MyCV/assets/profile.jpg';

export const HomeHero: React.FC<HomeHeroProps> = ({ onCopyResult, labelledById }) => {
  const { locale } = useI18n();
  const contact = (locale === 'cs' ? (csContact as ContactData) : (enContact as ContactData));
  const skills = (locale === 'cs' ? (csSkills as SkillsData) : (enSkills as SkillsData));

  const techs = useMemo(() => {
    return [...(skills.tech ?? []), ...(skills.tools ?? [])];
  }, [skills.tech, skills.tools]);

  const resolvedLocale = locale === 'cs' ? 'cs' : 'en';

  return (
    <section className="container" style={{ padding: '32px 0' }} aria-labelledby={labelledById}>
      <Box
        sx={{
          p: { xs: 2, md: 3 },
          borderRadius: 4,
          background:
            'linear-gradient(135deg, rgba(66,81,84,0.95) 0%, rgba(81,81,81,0.65) 50%, rgba(217,217,217,0.45) 100%)',
        }}
      >
        <Paper elevation={8} sx={{ p: { xs: 4, md: 6 }, backdropFilter: 'blur(8px)' }}>
          <Stack direction={{ xs: 'column', md: 'row' }} spacing={4} alignItems={{ xs: 'center', md: 'flex-start' }}>
            <Avatar src={profileSrc} alt="Profile" sx={{ width: 140, height: 140, borderRadius: 8 }}>
              J
            </Avatar>
            <Stack spacing={1} sx={{ flex: 1 }}>
              <Typography variant="h2" fontWeight={900} sx={{ lineHeight: 1.1 }}>
                {contact.fullName}
              </Typography>
              <Typography variant="h6" color="text.secondary">
                {contact.location}
              </Typography>
              <Box sx={{ mt: 2 }}>
                <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ rowGap: 1.25 }}>
                  {techs.map((tech, index) => (
                    <Chip
                      key={`${tech}-${index}`}
                      label={tech}
                      variant="filled"
                      sx={(theme) => ({
                        borderRadius: 9999,
                        bgcolor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.14)' : 'grey.100',
                        color: theme.palette.mode === 'dark' ? theme.palette.common.white : '#1b1b1b',
                        border: '1px solid',
                        borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.22)' : 'grey.200',
                      })}
                    />
                  ))}
                </Stack>
              </Box>
              <Divider sx={{ my: 2 }} />
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={1.5}>
                {contact.email ? (
                  <EmailCopyButton
                    email={contact.email}
                    locale={resolvedLocale}
                    onCopyResult={onCopyResult}
                    tooltip={locale === 'cs' ? 'Kliknutm zkoprujete e-mail' : 'Click to copy e-mail'}
                    buttonProps={{
                      variant: 'contained',
                      color: 'primary',
                      size: 'large',
                      startIcon: <MailOutlineIcon />,
                    }}
                  />
                ) : null}
                {contact.linkedin ? <LinkedInButton href={contact.linkedin} locale={resolvedLocale} /> : null}
              </Stack>
            </Stack>
          </Stack>
        </Paper>
      </Box>
    </section>
  );
};
