import React from 'react';
import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import { useScrollReveal } from '../../hooks/useScrollReveal';

export type ProjectEntry = {
  name: string;
  summary: string;
  url?: string;
  repo?: string;
  tech?: string[];
  highlights?: string[];
  demo?: string;
  link?: string;
  hideLink?: boolean;
};

type ProjectCardProps = {
  project: ProjectEntry;
  animationOrder?: number;
};

export const ProjectCard: React.FC<ProjectCardProps> = ({ project, animationOrder = 0 }) => {
  const rawLink = project.url ?? project.demo ?? project.link;
  const linkHref = !project.hideLink ? rawLink : undefined;
  const { ref, style } = useScrollReveal({
    direction: 'left',
    staggerIndex: animationOrder,
  });

  return (
    <Paper ref={ref} sx={{ p: 2.5 }} style={style}>
      <Typography variant="h5" sx={{ m: 0 }}>
        {project.name}
      </Typography>
      <Typography sx={{ mt: 1 }}>{project.summary}</Typography>
      {project.tech?.length ? (
        <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ mt: 1, rowGap: 1 }}>
          {project.tech.map((tech, index) => (
            <Chip
              key={`${tech}-${index}`}
              label={tech}
              size="small"
              sx={(theme) => ({
                borderRadius: 9999,
                bgcolor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.14)' : 'grey.100',
                color: theme.palette.mode === 'dark' ? theme.palette.common.white : '#1b1b1b',
                border: '1px solid',
                borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.22)' : 'grey.200',
              })}
            />
          ))}
        </Stack>
      ) : null}
      {project.highlights?.length ? (
        <ul style={{ marginTop: 8 }}>
          {project.highlights.map((highlight, index) => (
            <li key={index}>{highlight}</li>
          ))}
        </ul>
      ) : null}
      {linkHref ? (
        <Stack direction="row" spacing={1.5} sx={{ mt: 1 }}>
          <Button href={linkHref} target="_blank" rel="noreferrer" variant="contained">
            Link
          </Button>
        </Stack>
      ) : null}
    </Paper>
  );
};
