import React, { useMemo } from 'react';
import { ThemeProvider as MuiThemeProvider, createTheme, CssBaseline } from '@mui/material';
import { useTheme as useAppTheme } from '../context/ThemeContext';

const paletteBase = {
  primary: { main: '#425154' },
  secondary: { main: '#515151' },
  grey: { 100: '#d9d9d9' } as any,
};

export const AppThemeProvider: React.FC<{ children: React.ReactNode }>= ({ children }) => {
  const { theme } = useAppTheme();
  const muiTheme = useMemo(() => {
    const focusColor = '#40E0D0';
    return createTheme({
      palette: {
        mode: theme,
        ...paletteBase,
        background: theme === 'dark' ? { default: '#121212', paper: '#1e1e1e' } : { default: '#ffffff', paper: '#f9f9f9' },
        text: theme === 'dark' ? { primary: '#ffffff', secondary: '#ffffff' } : { primary: '#1b1b1b', secondary: '#515151' },
      },
      shape: { borderRadius: 14 },
      typography: {
        fontFamily: 'Montserrat, system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Helvetica Neue, Arial',
      },
      components: {
        MuiButton: { defaultProps: { disableRipple: false }, styleOverrides: { root: { textTransform: 'none', borderRadius: 9999 } } },
        MuiPaper: { styleOverrides: { rounded: { borderRadius: 20 } } },
        MuiChip: { styleOverrides: { root: { borderRadius: 9999 } } },
        MuiOutlinedInput: {
          styleOverrides: {
            root: ({ theme }) => ({
              backgroundColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.10)' : '#ffffff',
              '& fieldset': {
                borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.45)' : 'rgba(0,0,0,0.23)',
              },
              '&:hover fieldset': {
                borderColor: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : 'rgba(0,0,0,0.35)',
              },
              '&.Mui-focused fieldset': {
                borderColor: focusColor,
              },
            }),
          },
        },
        MuiInputBase: {
          styleOverrides: {
            input: ({ theme }) => ({
              color: theme.palette.mode === 'dark' ? '#ffffff' : 'inherit',
            }),
          },
        },
        MuiInputLabel: {
          styleOverrides: {
            root: ({ theme }) => ({
              color: theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.75)' : 'rgba(0,0,0,0.6)',
              '&.Mui-focused': {
                color: focusColor,
              },
            }),
          },
        },
      }
    });
  }, [theme]);

  return (
    <MuiThemeProvider theme={muiTheme}>
      <CssBaseline />
      {children}
    </MuiThemeProvider>
  );
};
