import React from 'react';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import csData from '../../data/cs/certifications.json';
import enData from '../../data/en/certifications.json';
import { useI18n } from '../../context/I18nContext';
import { Certification, CertificationCard } from '../CertificationCard';

type CertificationsProps = {
  labelledById?: string;
};

const sectionPadding = { padding: '24px 0' };

export const Certifications: React.FC<CertificationsProps> = ({ labelledById }) => {
  const { locale } = useI18n();
  const items = (locale === 'cs' ? csData : enData) as Certification[];

  return (
    <section className="container" style={sectionPadding} aria-labelledby={labelledById}>
      <Stack spacing={2}>
        {items.length === 0 ? (
          <Paper sx={{ p: 2.5 }}>
            <Typography>Brzy doplnim certifikace.</Typography>
          </Paper>
        ) : (
          items.map((cert, index) => (
            <CertificationCard key={`${cert.name}-${index}`} cert={cert} animationOrder={index} />
          ))
        )}
      </Stack>
    </section>
  );
};
