import React, { createContext, useContext, useEffect, useMemo, useState } from 'react';

export type Consent = {
  necessary: true; // always true
  preferences: boolean;
  analytics: boolean;
  marketing: boolean;
};

type ConsentContextType = {
  consent: Consent | null;
  setConsent: (c: Consent) => void;
};

const ConsentContext = createContext<ConsentContextType | undefined>(undefined);

const CONSENT_KEY = 'cookie-consent-v1';

export const ConsentProvider: React.FC<{ children: React.ReactNode }>
  = ({ children }) => {
  const [consent, setConsentState] = useState<Consent | null>(null);

  useEffect(() => {
    try {
      const raw = localStorage.getItem(CONSENT_KEY);
      if (raw) setConsentState(JSON.parse(raw));
    } catch { /* ignore */ }
  }, []);

  const setConsent = (c: Consent) => {
    setConsentState(c);
    try { localStorage.setItem(CONSENT_KEY, JSON.stringify(c)); } catch { /* ignore */ }
  };

  const value = useMemo(() => ({ consent, setConsent }), [consent]);
  return <ConsentContext.Provider value={value}>{children}</ConsentContext.Provider>;
};

export const useConsent = () => {
  const ctx = useContext(ConsentContext);
  if (!ctx) throw new Error('useConsent must be used within ConsentProvider');
  return ctx;
};

