import React from 'react';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

type ContactFormAlertProps = {
  open: boolean;
  onClose: () => void;
  locale: 'cs' | 'en';
  email: string;
};

const getAlertMessage = (locale: 'cs' | 'en', email: string) => {
  if (locale === 'cs') {
    return `Backend cast neni implementovana. Prosim, kontaktujte me na e-mailu ${email}.`;
  }
  return `Backend part is not implemented. Please contact me at ${email}.`;
};

export const ContactFormAlert: React.FC<ContactFormAlertProps> = ({ open, onClose, locale, email }) => {
  return (
    <Snackbar
      open={open}
      onClose={onClose}
      autoHideDuration={5000}
      anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
    >
      <Alert
        onClose={onClose}
        severity="warning"
        variant="filled"
        role="alert"
        aria-live="assertive"
        sx={{ width: '100%' }}
      >
        {getAlertMessage(locale, email)}
      </Alert>
    </Snackbar>
  );
};
