import React from 'react';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';

export type ToastProps = {
  open: boolean;
  message: string | null;
  onClose: () => void;
  severity?: 'success' | 'info' | 'warning' | 'error';
  autoHideMs?: number;
};

export const Toast: React.FC<ToastProps> = ({ open, message, onClose, severity = 'success', autoHideMs = 3000 }) => {
  return (
    <Snackbar
      open={open}
      onClose={onClose}
      autoHideDuration={autoHideMs}
      anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
    >
      <Alert
        onClose={onClose}
        severity={severity}
        variant="filled"
        role="status"
        aria-live="polite"
        sx={{ width: '100%' }}
      >
        {message}
      </Alert>
    </Snackbar>
  );
};

export default Toast;
