import React from 'react';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import { useScrollReveal } from '../../hooks/useScrollReveal';

export type EducationEntry = {
  institution: string;
  faculty: string;
  field: string;
  start: string;
  end: string | null;
  notes?: string[];
};

type EducationCardProps = {
  entry: EducationEntry;
  isCs: boolean;
  animationOrder?: number;
};

export const EducationCard: React.FC<EducationCardProps> = ({ entry, isCs, animationOrder = 0 }) => {
  const { ref, style } = useScrollReveal({
    direction: 'right',
    staggerIndex: animationOrder,
  });

  return (
    <Paper ref={ref} sx={{ p: 2.5 }} style={style}>
      <Typography variant="h5" sx={{ m: 0 }}>
        {entry.institution}
      </Typography>
      <Typography variant="body2" color="text.secondary">
        {entry.faculty} - {entry.field}
      </Typography>
      <Typography variant="body2" sx={{ mt: 1 }}>
        {entry.start} - {entry.end ?? (isCs ? 'soucasnost' : 'present')}
      </Typography>
      {entry.notes?.length ? (
        <ul>
          {entry.notes.map((note, index) => (
            <li key={index}>{note}</li>
          ))}
        </ul>
      ) : null}
    </Paper>
  );
};
