import React from 'react';
import Button from '@mui/material/Button';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import Tooltip from '@mui/material/Tooltip';

type LinkedInButtonProps = {
  href: string;
  locale: 'cs' | 'en';
};

export const LinkedInButton: React.FC<LinkedInButtonProps> = ({ href, locale }) => {
  const tooltipTitle = locale === 'cs' ? 'Otevrit LinkedIn profil' : 'Open LinkedIn profile';

  return (
    <Tooltip title={tooltipTitle}>
      <Button
        href={href}
        target="_blank"
        rel="noreferrer"
        startIcon={<LinkedInIcon />}
        size="large"
        variant="contained"
        color="primary"
        aria-label={tooltipTitle}
      >
        LinkedIn
      </Button>
    </Tooltip>
  );
};

