import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Tooltip from '@mui/material/Tooltip';
import Typography from '@mui/material/Typography';
import DownloadCVButton from '../../components/DownloadCVButton/DownloadCVButton';
import Toast from '../../components/Toast/Toast';
import { HomeHero } from '../../components/HomeHero';
import { HomeAbout } from '../../components/HomeAbout';
import { useI18n } from '../../context/I18nContext';

export const HomePage: React.FC = () => {
  const { locale, t } = useI18n();
  const [copied, setCopied] = useState<string | null>(null);
  const [severity, setSeverity] = useState<'success' | 'error'>('success');
  const isCs = locale === 'cs';
  const headingId = 'home-heading';
  const subheadingId = 'home-subheading';

  const handleCopyResult = (message: string, resultSeverity: 'success' | 'error') => {
    setSeverity(resultSeverity);
    setCopied(message);
  };

  return (
    <>
      <Box
        component="section"
        className="container"
        aria-labelledby={headingId}
        sx={{
          padding: { xs: '24px 0 12px', md: '32px 0 16px' },
          textAlign: 'center',
        }}
      >
        <Typography
          component="h1"
          variant="h2"
          id={headingId}
          sx={{ fontWeight: 900, lineHeight: 1.1 }}
        >
          {t('home_heading')}
        </Typography>
        <Typography
          component="p"
          variant="subtitle1"
          id={subheadingId}
          sx={{ mt: 0.5, color: 'text.secondary' }}
        >
          {t('home_subheading')}
        </Typography>
      </Box>
      <HomeHero onCopyResult={handleCopyResult} labelledById={headingId} />
      <section className="container" style={{ padding: '0 0 32px' }}>
        <Stack spacing={2.5} sx={{ mt: 3 }}>
          <HomeAbout />
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 8 }}>
            <Tooltip title={isCs ? 'Stahnout CV' : 'Download CV'}>
              <span style={{ display: 'inline-flex' }}>
                <DownloadCVButton label={isCs ? 'Stahnout CV' : 'Download CV'} />
              </span>
            </Tooltip>
          </Box>
        </Stack>
      </section>
      <Toast open={!!copied} message={copied} onClose={() => setCopied(null)} severity={severity} />
    </>
  );
};
